@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li{
    @apply px-2;
}
.bg-primary {
    background-color: rgb(80, 3, 76);
}
.bg-herocolor{
    background-color: #8b6183;
}

.bg-secondary{
    background: #000000;
}

.bg-sy{
    background: #51417b;
}

.mobile-nav{
    @apply fixed top-10 left-0 bg-primary w-full h-1/3;
}
.mobile-nav li{
    @apply w-full h-full text-center border-b-2;
}
.project-dec{
    @apply absolute left-0  top-0 right-0 bottom-[45px] top-[15px] bg-slate-400 opacity-0 duration-500 hover:opacity-100 px-3;
}
.btn{
    @apply  bg-slate-900 px-5 py-5 font-bold hover:border-2 rounded; 
}
html{
    scroll-behavior: smooth;
}










